.form-horizontal .radio {
  margin-bottom: 10px;
}

.radio {
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;

  label {
    cursor: pointer;
    padding-left: 45px;
    position: relative;
    font-size: 14px;
    font-weight: bold;

    span {
      display: block;
      position: absolute;
      left: 10px;
      top: 2px;
      transition-duration: 0.2s;
    }

    .circle {
      border: 2px solid rgba(0, 0, 0, 0.84);
      height: 15px;
      width: 15px;
      border-radius: 100%;
    }

    .check {
      height: 15px;
      width: 15px;
      border-radius: 100%;
      background-color: rgba(0, 0, 0, 0.84);
      transform: scale(0);
    }

    .check:after {
      display: block;
      position: absolute;
      content: "";
      background-color: rgba(0, 0, 0, 0.84);
      left: -18px;
      top: -18px;
      height: 50px;
      width: 50px;
      border-radius: 100%;
      z-index: 1;
      opacity: 0;
      margin: 0;
      transform: scale(1.5);
    }

    input[type=radio]:not(:checked) ~ .check:after {
      -webkit-animation: rippleOff 500ms;
      animation: rippleOff 500ms;
    }

    input[type=radio]:checked ~ .check:after {
      -webkit-animation: rippleOn 500ms;
      animation: rippleOn 500ms;
    }
  }
}

.radio input[type=radio]:checked ~ .check,
.radio-default input[type=radio]:checked ~ .check {
  background-color: rgba(0, 0, 0, 0.84);
}

.radio-primary input[type=radio]:checked ~ .check {
  background-color: #4285f4;
